@import url(https://fonts.googleapis.com/css2?family=Zen+Dots&display=swap);
:root {
  --logo-color: #5cdb95;
  --text-color: #065aaf;
  --neutral-color: #edf5e1;
  --dark-color:#1a1a1a;
  --semi-grey-color:#a6aaad;
  --grey-color:#4b4949;
  --box-shadow-color:#a8a8a8;
}

html, * {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
}
body {
  font-family: "Quicksand", sans-serif, "Franklin Gothic Medium", "Arial Narrow",
    Arial;
  color: #065aaf;
  color: var(--text-color);
}

/*                utility               */
.center {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.d-block{
  display: block;
}
.text-align-center {
  text-align: center;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
  color: #065aaf;
  color: var(--text-color);
}

.hover:hover {
  color: #05386b;
  border-bottom: 2px solid #065aaf;
  border-bottom: 2px solid var(--text-color);
}

/******************************************/


/*         navbar-section         */
#navbar {
  max-width: 1850px;
  min-height: 66px;
  box-shadow: 0 0 12px var(--box-shadow-color);
  padding: 0 1.5rem;
  margin: auto;
}

/*         navbar-utility         */
#navbar ul li {
  padding: 0.9rem;
  margin: 0 0.1rem;
  cursor: pointer;
  overflow: hidden;
}

/*         navbar-morpher         */
.logo::before {
  content: " ";
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 50px;
  height: 50px;
  margin: auto;
  z-index: 1;
  background-color: #5cdb95;
  background-image: linear-gradient(19deg, #5cdb95 0%, #3fb474 100%);
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  -webkit-animation: morphing 10s infinite;
          animation: morphing 10s infinite;
  overflow: hidden;
}
@-webkit-keyframes morphing {
  0% {
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
    box-shadow: 15px 15px 50px rgba(0, 0, 0, 0.2);
  }
  25% {
    border-radius: 58% 42% 75% 25% / 76% 46% 54% 24%;
  }
  50% {
    border-radius: 50% 50% 33% 67% / 55% 27% 73% 45%;
    box-shadow: -10px -5px 50px rgba(0, 0, 0, 0.2);
  }
  75% {
    border-radius: 33% 67% 58% 42% / 63% 68% 32% 37%;
  }
}
@keyframes morphing {
  0% {
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
    box-shadow: 15px 15px 50px rgba(0, 0, 0, 0.2);
  }
  25% {
    border-radius: 58% 42% 75% 25% / 76% 46% 54% 24%;
  }
  50% {
    border-radius: 50% 50% 33% 67% / 55% 27% 73% 45%;
    box-shadow: -10px -5px 50px rgba(0, 0, 0, 0.2);
  }
  75% {
    border-radius: 33% 67% 58% 42% / 63% 68% 32% 37%;
  }
}

/*         navbar-brand         */
.logo h1 {
  color: var(--logo-color);
  font-family: "Zen Dots", cursive;
  margin-right: 1.3rem;
  cursor: pointer;
}

/*         navbar-search-section         */
.search-box input[type="search"] {
  display: block;
  width: 600px;
  height: 2.5rem;
  background-color: #e9e9e9;
  border-radius: 23px;
  font-size: 0.9rem;
  padding-left: 15px;
}
.search input:hover {
  border: 1px solid #a8a8a8;
}

/*         navbar-section list-1 (eg : Home part)         */
.list-1 {
  margin-left: auto;
}

/*         navbar-section-divider ===>  " | "         */
.divider {
  width: 1px;
  height: 30px;
  background-color: var(--dark-color);
}

/*         navbar-section-hamburger         */
.hamburger-icon span {
  display: none;
  font-size: 50px;
  color: var(--logo-color);
  text-shadow: 0px 0px 3px #a8a8a8;
}

/*         navbar-responsive         */

@media (max-width: 1348px) {
  .search-box input[type="search"] {
    max-width: 400px;
  }
}

@media (max-width: 1130px) {
  #navbar ul li {
    font-size: 0.9rem;
    padding: 0.6rem;
  }
}

@media (max-width: 1045px) {
  #navbar {
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }
  .search-box {
    display: none;
  }
  .list-1 {
    margin: 0;
  }
  .list-divider {
    display: none;
  }
}

@media (max-width: 700px) {
  #navbar {
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  .logo h1 {
    font-size: 1.3rem;
  }
  .logo::before {
    width: 40px;
    height: 40px;
  }
  #navbar .list {
    display: none;
  }
  .hamburger-icon span {
    display: block;
  }
}

@media (max-width: 300px) {
  #navbar {
    padding: 0 0.5rem;
  }
  .logo::before {
    width: 30px;
    height: 30px;
  }
  .hamburger-icon span {
    font-size: 30px;
  }
}

#header {
  max-width: 100%;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-family: -apple-system, BlinkMacSystemFont, segoe ui, roboto, oxygen,
    cantarell, helvetica neue, ubuntu, sans-serif;
  padding: 3rem;
}
#header .head {
  margin: auto;
  font-size: 26px;
  color: var(--dark-color);
  font-weight: 800;
}
#header .paragraph-subtitle-1 {
  margin: 1.5rem 9.5rem;
  font-size: 15px;
  color: var(--semi-grey-color);
}
#header .paragraph-subtitle-2 {
  margin: auto;
  font-size: 18px;
  color: var(--grey-color);
}

@media (min-width: 1900px) {
  #header {
    max-width: 1850px;
    margin: auto;
  }
}
@media (max-width: 1366px) {
  #header .paragraph-subtitle-1 {
    margin: 1.5rem;
  }
}
@media (max-width: 580px) {
  #header {
    padding: 2rem 0.5rem 0 0.5rem;
  }
  #header .head{
    margin: 0;
  }
  #header .paragraph-subtitle-1 {
    margin: 1.5rem 0;
  }
  #header .paragraph-subtitle-1,
  #header .paragraph-subtitle-2 {
    font-size: 15px;
    text-align: center;
  }
}

#gallery-section {
  margin-top: 18px;
}
.column {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  padding: 0 12px;
  margin: 0 1rem;
}
.column_images {
  max-width: 100%;
  padding: 0 4px;
}

.column_images img {
  max-width: 100%;
  margin-top: 5px;
}
@media (min-width: 1900px) {
  #gallery-section {
    max-width: 1850px;
    margin: auto;
  }
}
@media (max-width: 1100px) {
  .column {
    grid-template-columns: repeat(2, 1fr);
  }
}

/*            footer-utility             */
a {
  color: var(--semi-grey-color);
}

/*          footer-section            */
#footer {
  width: 100%;
  font-family: sans-serif;
  padding: 1.5rem 0;
  background: linear-gradient(50deg, #1a1a1a, #131212);
  background-size: 400% 400%;
  -webkit-animation: bg-animate 25s ease infinite;
          animation: bg-animate 25s ease infinite;
}
@-webkit-keyframes bg-animate {
  0%{background-position:0% 83%}
  50%{background-position:100% 18%}
  100%{background-position:0% 83%}
}
@keyframes bg-animate {
  0%{background-position:0% 83%}
  50%{background-position:100% 18%}
  100%{background-position:0% 83%}
}

.footer,
.footer-copyright {
  max-width: 1060px;
  margin: 0 auto;
}
.footer {
  display: -webkit-flex;
  display: flex;
  padding: 1rem;
  margin-bottom: 2rem;
}
.f-1,
.f-2,
.f-3 {
  width: 250px;
  -webkit-flex-direction: column;
          flex-direction: column;
  line-height: 23px;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}
.f-1 {
  margin-right: 8rem;
}
.f-2 h3,
.f-3 h3 {
  width: 160px;
  border-bottom: 2px solid var(--text-color);
  padding-bottom: 0.5rem;
}
.f-1 .logo-story,
.f-2 ul,
.f-3 ul {
  margin-top: 0.7rem;
}
.footer-copyright-line {
  display: block;
  margin: auto;
}
.logo-story,
.f-1,
.f-2,
.f-3,
.f-4,
.footer-copyright-line {
  font-size: 13px;
  color: var(--semi-grey-color);
}
.footer-copyright {
  width: 100%;
  padding: 1.5rem;
  border-top: 1px solid var(--semi-grey-color);
}

@media (max-width: 1100px) {
  .footer{
    max-width: 850px;
  }
  .f-1 {
    margin-right: 5rem;
    width: 300px;
}
  .footer-copyright {
    max-width: 90%;
  }
}
@media(max-width:769px){
  .footer{
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .f-1, .f-2, .f-3{
    width: 100%;
    margin: auto;
}
.f-2, .f-3{
  margin-top: 1.5rem;
}
.f-2 h3,
.f-3 h3 {
  max-width: 260px;
  border-bottom: 2px solid var(--text-color);
  padding-bottom: 0.5rem;
}
.f-2 ul,
.f-3 ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.f-2 ul li,
.f-3 ul li {
  display: block;
  margin-right:1rem;
}
}
@media(max-width:479px){
  .f-2 ul li,
.f-3 ul li {
  margin-right:2rem;
}
}
